import React, { ChangeEvent, DetailedHTMLProps, InputHTMLAttributes, KeyboardEvent } from 'react';
import './SuperInput.scss';

//    
type DefaultInputPropsType = DetailedHTMLProps<
  InputHTMLAttributes<HTMLInputElement>,
  HTMLInputElement
>;

//               
// (   value: string, onChange: ...;      DefaultInputPropsType)
type SuperInputTextPropsType = DefaultInputPropsType & {
  //  +       
  onChangeText?: (value: string) => void;
  onEnter?: () => void;
  error?: string;
  spanClassName?: string;
  inputName?: string;
  type?: string;
  onChangeCall?: (e: ChangeEvent<HTMLInputElement>) => void;
  value: string;
  width?: string;
};

const SuperInput: React.FC<SuperInputTextPropsType> = ({
  onChange,
  onChangeText,
  onKeyPress,
  onEnter,
  error,
  className,
  spanClassName,
  inputName,
  type,
  onChangeCall,
  value,
  width,
  ...restProps //       restProps
}) => {
  const onChangeCallback = (e: ChangeEvent<HTMLInputElement>) => {
    onChange && //    onChange
      onChange(e); //     ( onChange  )

    onChangeText && onChangeText(e.currentTarget.value);
  };
  const onKeyPressCallback = (e: KeyboardEvent<HTMLInputElement>) => {
    onKeyPress && onKeyPress(e);

    e.key === 'Enter' && //    Enter
      onEnter && //    onEnter
      onEnter(); //   
  };

  const finalInputClassName = `${'superInput'} ${className ? className : ''} ${
    error ? 'superInput__errorInput' : ''
  }`;

  return (
    <div className="wrapper__input">
      <span className="form__group field">
        <input
          className="form__field"
          type={type}
          name={type}
          required
          onChange={onChangeCallback}
          onKeyPress={onKeyPressCallback}
          value={value}
          style={{ width: width }}
          {...restProps}
        />
        <label htmlFor="name" className="form__label">
          {inputName}
        </label>
      </span>
      {error && <span className={finalInputClassName}>{error}</span>}
    </div>
  );
};

export default SuperInput;
